    border-radius: 6px;
}

.features-details .features-item .more-btn:hover {
    background-color: color-mix(in srgb, var(--accent-color), transparent 20%);
}

.features-details .features-item ul {
    list-style: none;
    padding: 0;
}

.features-details .features-item ul li {
